
/* Розділ resset (тимчасовий) */

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

/* Розділ базових налаштвань (тимчасовий) */

html {
  scroll-behavior: smooth;
} 

/* позиціонування елементів */

.hero {
  border: 0;
  background-color: #84A278;
}

/* фонове зображення */

.hero--overlay {
  
  margin: 0 auto;
    
  @media screen and (max-width: 680px) {
    background-size: 177px 217px, contain;
    background-repeat: no-repeat, no-repeat;
    background-position: bottom left 83px, bottom;
    max-width: 320px;
    min-height: 460px;
  }

  @media screen and (min-width: 681px) and (max-width: 1200px) {
    background-size: 152px 152px, contain;
    background-repeat: no-repeat, no-repeat;
    background-position: left 32px bottom 12px, bottom;
    max-width: 768px;
    min-height: 355px;
  }
  
  @media screen and (min-width: 1200px) {
    background-size: 225px 225px, 1200px 383px;
    background-repeat: no-repeat, no-repeat;
    background-position: left 16px bottom 64px, bottom;

    max-width: 1200px;
    min-height: 591px;
  }
}

.hero--overlay {
    background-image: url("../images/img-hero/png/mobile-smile-icecream.png"),
    url("../images/img-hero/png/mobile-point-line.png");
}

@media (min-resolution: 2dppx) {
  .hero--overlay {
    background-image: url("../images/img-hero/png/mobile-smile-icecream-2x.png"),
    url("../images/img-hero/png/mobile-point-line-2x.png");
    }
}

@media (min-width: 681px) {
  .hero--overlay {
    background-image: url("../images/img-hero/png/tablet-hero-plate-icecream.png"),
      url("../images/img-hero/png/tablet-point-line.png");
  }

  @media (min-resolution: 2dppx) {
    .hero--overlay {
      background-image: url("../images/img-hero/png/tablet-hero-plate-icecream-2x.png"),
        url("../images/img-hero/png/tablet-point-line-2x.png");
    }
  }
}

@media (min-width: 1200px) {
  .hero--overlay {
    background-image: url("../images/img-hero/png/hero-plate-icecream.png"),
      url("../images/img-hero/png/point-line.png");
    
  }
    
  @media (min-resolution: 2dppx) {
    .hero--overlay {
      background-image: url("../images/img-hero/png/hero-plate-icecream-2x.png"),
        url("../images/img-hero/png/point-line-2x.png");
    }
  }
}

.containerr {
  display: flex;
  justify-content: space-between;

  max-width: 1200px;
  margin-right: auto;
  margin-left: auto;
  padding: 0 16px 0 16px;
  border: 0;

  @media screen and (max-width: 680px) {
    display: block;
    min-width: 247px;
    padding-top: 50px;
  }

  @media screen and (min-width: 681px) and (max-width: 1199px) {
    padding-top: 14px;
  }
}

.hero__title {
  font-family: Seymour One, sans-serif;
  font-style: normal;
  font-weight: 400;
  
  letter-spacing: 0.04em;
  text-transform: uppercase;
  
  color: #FCFCFC;

  @media screen and (max-width: 680px) {
    display: block;
    min-width: 247px;
    font-size: 26px;
    line-height: 1.3;
  
    .hero__title--passion {
      font-size: 34px;
      line-height: 0.97;
    }
  }

  @media screen and (min-width: 681px) and (max-width: 1200px) {
    width: 247px;
    
    font-size: 26px;
    line-height: 1.3;
    letter-spacing: 0.04em;

    .hero__title--passion {
      font-size: 34px;
      line-height: 1;
    }
  }

  @media (min-width: 1200px) {
    margin-top: 45px;
    width: 367px;
    font-size: 38px;
    line-height: 1.26;

    .hero__title--passion {
      font-size: 38px;
      line-height: 1.26;
    }
  }
}

.guide-wrap {
  display: flex;
  margin-top: 28px;
  
  @media screen and (max-width: 680px) {
    margin-top: 32px;
  }
}

.guide-wrap__list {
  display: flex;

  font-family: Montserrat, sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  text-align: center;
}

.guide-wrap__item-btn {
  min-width: 134px;
  min-height: 42px;
  
  
  margin-right: 12px;
  
  .guide-wrap__link {
    width: 100%;
    height: 100%;

    padding: 10px 32px;
    line-height: 1.3;
    color: #131313;

    border: 2px solid;
    border-color: #F8D24E;
    border-radius: 8px;
    
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1), border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    
    &:hover {
      border-color: #0A8817;
    }
    
    &:focus {
      border-color: #0A8817;
      color: #FCFCFC;
    }
  }

  @media screen and (max-width: 1199px) {
    min-width: 108px;
    min-height: 38px;
    
    font-size: 12px;

    .guide-wrap__link {
      padding: 9px 24px;
    }
  }

}

.guide-wrap__item-ref {
  min-width: 154px;
  min-height: 40px;
  
  
  margin-right: 12px;
   
  text-align: center;

  .guide-wrap__link {
    line-height: 1.2;
    text-decoration-line: underline;

    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1), border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    
    color: #F8D24E;
    &:hover {
      color: #0A8817;
    }

    &:focus {
      color: #0A8817;
    }
   }

  @media screen and (max-width: 1199px) {
    min-width: 114px;
    min-height: 24px;
    

    .guide-wrap__link {
      font-size: 12px;
      line-height: 1.25;
    
      padding: 0;
    }   
 }
}

.features-wrap {
  margin-top: 109px;
  margin-left: 241px;
  width: 200px;

  @media screen and (max-width: 680px) {
    display: none;
  }

  @media screen and (min-width: 681px) and (max-width: 1199px) {
    margin-top: 46px;
    margin-left: 200px;
    width: 139px;
  }
}

.overlay-wrap {
  align-self: flex-end;

  position: relative;
  overflow: hidden;
  height: 80px;

  @media screen and (min-width: 681px) and (max-width: 1199px) {
    height: 70px;
  }
}

.hero-features__icon {
  margin-top: 24px;

  @media screen and (min-width: 681px) and (max-width: 1199px) {
    margin-top: 12px;
  }
}

.hero-features__overlay {
  width: 200px;
    
  position: absolute;
  top: 0;
  left: 0;

  transform: translateY(100%);
  transition: transform 250ms;
}

.hero-features__text {
  font-family: Montserrat, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;

  color: #FCFCFC;

  @media screen and (max-width: 680px) {
    font-size: 12px;
  }
  
  @media screen and (min-width: 681px) and (max-width: 1199px) {
    font-size: 12px;  
  }
}

.features-wrap__link {
  width: 100%;
}

.features-wrap__link:hover .hero-features__overlay {
  transform: translateY(0);
  transition: transform 250ms;
}

.features-wrap__link:focus .hero-features__overlay {
  transform: translateY(0);
  transition: transform 250ms;
}

.hero__right-wrap {
  display: flex;

  @media screen and (max-width: 680px) {
    display: none;
  }
}

.image-wrap {
  margin-top: 31px;

  @media screen and (min-width: 681px) and (max-width: 1199px) {
    margin-top: 12px;
  }
}

.hero-main__image {
  @media screen and (max-width: 680px) {
    width: 177px;
    height: 217px;
  }
  
  @media screen and (min-width: 681px) and (max-width: 1199px) {
    width: 268px;
    height: 329px;
  }
  
  @media screen and (min-width: 1200px) {
    width: 456px;
    height: 560px;
  }
}

.hero-service__wrap {
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    width: 46px;
  }
  @media screen and (max-width: 1200px) {
    width: 79px;
  }
}

.hero-service__item-cafes {
  margin-top: 53px;
  margin-left: 24px;

  text-align: right;
  @media screen and (min-width: 681px) and (max-width: 1199px) {
    margin-right: 16px;
  }
}

.hero-service__item-trucks {
 margin-top: 16px;

  text-align: right;

  @media screen and (min-width: 681px) and (max-width: 1199px) {
    margin-right: 16px;
  }
}

.hero-service__numb {
  font-family: Montserrat, sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 46px;
  line-height: 1.2;
  /* identical to box height */

  letter-spacing: 0.04em;
  text-transform: uppercase;
  
  color: #131313;

  @media screen and (min-width: 681px) and (max-width: 1199px) {
    font-size: 28px;
  }
}

.hero-service__text {
  font-family: Montserrat, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
  
  text-align: right;
  
  color: #FCFCFC;

  @media screen and (min-width: 681px) and (max-width: 1199px) {
    font-size: 8px;
    line-height: 1.25;
  }
}

/* анімація */

@keyframes top-to-bottm {
  0% {
    opacity: 0;
    transform: translateY(-100px);
  }

  50% {
    opacity: 0.6;
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bottm-to-top {
  0% {
    opacity: 0;
    transform: translateY(560px);
    rotate: (0deg);
  }

  25% {
    opacity: 0.3;
  }

  50% {
    opacity: 0.6;
  }

  100% {
    opacity: 1;
    transform: translateY(0);
    rotate: (360deg);
  }
}

@keyframes left-to-right {
  0% {
    opacity: 0;
    transform: translateX(-100px);
  }

  50% {
    opacity: 0.6;
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.hero__title--move {
  animation: top-to-bottm 1000ms ease 250ms 1 both;
}

.hero-main__image--mov {
  animation: bottm-to-top 1000ms ease 500ms 1 both;
}

.icon--move {
  animation: left-to-right 1000ms ease 1250ms 1 both;
}
