  // MOOOOOOOOOOOOOOOOOODALLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLLL


  :root {
    /* background colors */
    --main-background-color: #817C7C;
    --secondary-background-color: #FCFCFC;
    /* text colors */
    --main-text-color: #131313;
    --secondary-text-color: #FCFCFC;
    /* button text colors */
    --yellow-btn-text-color: #F8D24E;
    --black-btn-text-color: #000000;
    --dark-btn-text-color: #131313;
    --white-btn-text-color: #FFFFFF;
    /* button background colors */
    --default-btn-bg-color: #F8D24E;
    --hover-btn-bg-color: #0A8817;
    --focus-btn-bg-color: #0A8817;
    --disabled-btn-bg-color: #ABABAB;
    /* media button colors */
    --main-media-btn-bg-color: #F8D24E;
    --default-media-btn-bg-color: #84A178;
    --hover-media-btn-bg-color: #FCFCFC;
    --focus-media-btn-bg-color: #FCFCFC;
    /* border outline modal color */
    --border-outline-modal-color: #F8D24E;
  }

  *,
  *::before,
  *::after {
    box-sizing: border-box;
  }
  .backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    opacity: 1;
  }
  .backdrop.is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }

  .modal-franch,
  .modal-locat{
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 568px;
    background-color: var(--secondary-background-color);
    border: 2px solid var(--border-outline-modal-color);
    border-radius: 10px;
    padding: 31px 60px;
  }
  .modal-form {
    margin-left: 40px;
    margin-right: 40px;
  }
  .modal__btn{
    position: absolute;
    top: 12px;
    right: 12px;
    cursor: pointer;
    background-color: var(--secondary-background-color);
    color: var(--dark-btn-text-color);
    border:none;
    padding: 0;
        cursor: url(../images/cursor.png), pointer;
    &:hover,
    &:focus,
    &:focus-visible {
      color: var(--border-outline-modal-color);
    }
  }
  .icon-cloce-black {
    width: 24px;
    height: 24px;
    &:hover,
    &:focus,
    &:focus-visible {
      fill: var(--border-outline-modal-color);
    }
  }
  .button-modal-submit{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 145px;
    color: var(--dark-btn-text-color);
    background-color: var(--default-btn-bg-color);
    border-radius: 10px;
    padding: 12px 34px;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.21;
    border: none;
    margin-top: 32px;
  cursor: url(../images/cursor.png), pointer;
      &:focus {
      background-color: var(--hover-btn-bg-color);
      color: var(--white-btn-text-color);
    }
    &:hover {
      background-color: var(--hover-btn-bg-color);
      color: var(--black-btn-text-color);
    }
  }
  .modal-heading{
    font-family: 'Seymour One';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.4;
    padding: 0 61px 32px 61px;
    margin: 0;
    align-items: center;
    text-align: center;
  }
  .modal-heading-franchise {
    font-family: 'Seymour One';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.4;
    padding: 0 61px 32px 61px;
    margin: 0;
    align-items: center;
    text-align: center;
  }
  .form__field {
    position: relative;
    display: flex;
    flex-direction: column;
  }
  .modal__text{
    font-family: 'Montserrat';
    font-weight: 400;
    font-size: 16px;
    line-height: 1.375;
    width: 448px;
    text-align: start;
    margin-left: auto;
    margin-right: auto;
    padding-left: 0;
    padding-right: 0;
    margin-bottom: 30px;
  }
  .modal__input {
    font-family: 'Montserrat';
    font-weight: 400;
    font-size: 16px;
    line-height: 1.42;
    position: relative;
    display: flex;
    background-color: rgba(248, 210, 78, 0.1);
    border: 1px solid #84A178;
    border-radius: 10px;
    height: 42px;
    margin-bottom: 14px;
    padding-left: 16px;
    &:hover,
    &:focus,
    &:focus-visible {
    border-color: var(--border-outline-modal-color);
  }
  }
  .modal__textarea {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 84px;
    border: 1px solid #84A178;
    border-radius: 10px;
    resize: none;
    margin-bottom: 31px;
  &:hover,
  &:focus,
  &:focus-visible {
    border-color: var(--border-outline-modal-color);
  }
  }
  .modal__form-message{
    display: block;
    resize: none;
    font-family: 'Montserrat';
    font-weight: 400;
    font-size: 16px;
    line-height: 1.42;
    outline: 1px solid transparent;
    outline-offset: -1px;
    border: none;
  }

  .map-active {
    position: absolute;
    width: 100%;
    height: 100%;
  }

